import App from './App'

// #ifndef VUE3
import Vue from 'vue'
import {
	createPinia
} from 'pinia';
import './uni.promisify.adaptor'
Vue.config.productionTip = false
App.mpType = 'app'
const app = new Vue({
	...App
})
const pinia = createPinia()
app.use(pinia)
app.$mount()
// #endif

// #ifdef VUE3
import {
	createSSRApp
} from 'vue'
export function createApp() {
	const app = createSSRApp(App)
	return {
		app
	}
}
// #endif